interface OpenAIResponseArgs {
  model?: string;
  input: string;
}

interface OpenAIResponse {
  id?: string;
  object?: string;
  created?: number;
  model?: string;
  choices?: Array<{
    index: number;
    message: {
      role: string;
      content: string;
    };
    finish_reason: string;
  }>;
  usage?: {
    prompt_tokens: number;
    completion_tokens: number;
    total_tokens: number;
  };
  error?: string;
  // 新版本API的字段
  status?: string;
  output?: Array<{
    id: string;
    type: string;
    content?: Array<{
      type: string;
      text: string;
    }>;
  }>;
}

export async function openaiResponse(
  args: OpenAIResponseArgs,
  env: { OPENAI_API_KEY: string }
): Promise<OpenAIResponse> {
  try {
    const { model = 'gpt-3.5-turbo', input } = args;
    
    console.log('OpenAI API Request:', { model, input: input.substring(0, 100) + '...' });

    // 使用标准的OpenAI Chat Completions API
    const response = await fetch('https://api.openai.com/v1/chat/completions', {
      method: 'POST',
      headers: {
        'Content-Type': 'application/json',
        'Authorization': `Bearer ${env.OPENAI_API_KEY}`
      },
      body: JSON.stringify({
        model,
        messages: [{
          role: 'user',
          content: input
        }],
        max_tokens: 1000
      })
    });

    console.log('OpenAI API Response Status:', response.status, response.statusText);
    
    if (!response.ok) {
      const errorText = await response.text();
      console.error('OpenAI API Error Response:', errorText);
      return {
        error: `OpenAI API Error: ${response.status} - ${errorText}`
      };
    }

    const data = await response.json() as any;
    console.log('OpenAI API Response Data:', JSON.stringify(data, null, 2));
    
    // 检查是否是新版本API响应格式
    if (data.output && Array.isArray(data.output)) {
      // 新版本API格式，转换为标准格式
      const messageOutput = data.output.find((item: any) => item.type === 'message');
      const textContent = messageOutput?.content?.find((item: any) => item.type === 'output_text');
      
      return {
        id: data.id,
        object: data.object || 'chat.completion',
        created: data.created_at || Math.floor(Date.now() / 1000),
        model: data.model,
        choices: [{
          index: 0,
          message: {
            role: 'assistant',
            content: textContent?.text || ''
          },
          finish_reason: data.status === 'completed' ? 'stop' : 'length'
        }],
        usage: {
          prompt_tokens: data.usage?.input_tokens || 0,
          completion_tokens: data.usage?.output_tokens || 0,
          total_tokens: data.usage?.total_tokens || 0
        }
      };
    } else {
      // 标准的OpenAI API响应格式
      return {
        id: data.id,
        object: data.object,
        created: data.created,
        model: data.model,
        choices: data.choices,
        usage: data.usage
      };
    }

  } catch (error) {
    console.error('OpenAI API Error:', error);
    return {
      id: undefined,
      object: undefined,
      created: undefined,
      model: undefined,
      choices: undefined,
      usage: undefined,
      error: `Request failed: ${error instanceof Error ? error.message : 'Unknown error'}`
    };
  }
}

// 如果你想使用标准的OpenAI Chat Completions API，可以使用下面这个函数
export async function openaiChatCompletions(
  args: { messages: Array<{ role: string; content: string }>, model?: string },
  env: { OPENAI_API_KEY: string }
): Promise<any> {
  try {
    const { messages, model = 'gpt-3.5-turbo' } = args;

    const response = await fetch('https://api.openai.com/v1/chat/completions', {
      method: 'POST',
      headers: {
        'Content-Type': 'application/json',
        'Authorization': `Bearer ${env.OPENAI_API_KEY}`
      },
      body: JSON.stringify({
        model,
        messages
      })
    });

    if (!response.ok) {
      const errorText = await response.text();
      throw new Error(`OpenAI API Error: ${response.status} - ${errorText}`);
    }

    return await response.json();

  } catch (error) {
    console.error('OpenAI Chat Completions Error:', error);
    throw error;
  }
}